.weui-toast {
  position: fixed;
  z-index: 5001;
  width: 7.6em;
  min-height: 7.6em;
  top: 180px;
  left: 50%;
  margin-left: -3.8em;
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  border-radius: 5px;
  color: #FFFFFF;
}
.weui-icon_toast {
  margin: 22px 0 0;
  display: block;
}
.weui-icon_toast.weui-icon-success-no-circle:before {
  color: #FFFFFF;
  font-size: 55px;
}
.weui-icon_toast.weui-loading {
  margin: 30px 0 0;
  width: 38px;
  height: 38px;
  vertical-align: baseline;
}
.weui-toast__content {
  margin: 0 0 15px;
}
.weui-toast.vux-toast-top {
  top: 10px;
}
.weui-toast.vux-toast-bottom {
  top: auto;
  bottom: 10px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.weui-toast.vux-toast-middle {
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.vux-slide-from-top-enter,
.vux-slide-from-top-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-100%) !important;
          transform: translateX(-50%) translateY(-100%) !important;
}
.vux-slide-from-bottom-enter,
.vux-slide-from-bottom-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(100%) !important;
          transform: translateX(-50%) translateY(100%) !important;
}
.vux-slide-from-top-enter-active,
.vux-slide-from-top-leave-active,
.vux-slide-from-bottom-enter-active,
.vux-slide-from-bottom-leave-active {
  -webkit-transition: all 400ms cubic-bezier(0.36, 0.66, 0.04, 1);
  transition: all 400ms cubic-bezier(0.36, 0.66, 0.04, 1);
}
.weui-toast {
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-left: 0!important;
}
.weui-toast.weui-toast_forbidden {
  color: #F76260;
}
.weui-toast.weui-toast_forbidden .weui-toast__content {
  margin-top: 10px;
}
.weui-toast.weui-toast_text {
  min-height: 0;
}
.weui-toast_text .weui-toast__content {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 15px;
}
.weui-toast__content {
  font-size: 16px;
}
.weui-loading_toast .weui-toast__content {
  margin-top: 0;
}
.weui-toast_success .weui-icon_toast:before {
  content: "\EA08";
}
.weui-toast_cancel .weui-icon_toast:before {
  content: "\EA0D";
}
.weui-toast_forbidden .weui-icon_toast.weui-icon-success-no-circle:before {
  content: "\EA0B";
  color: #F76260;
}
/* 重置 */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, input, select {margin: 0;padding: 0;border: 0;font: inherit;vertical-align: baseline;}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display: block;}
ol, ul {list-style: none;}
blockquote, q {quotes: none;}
blockquote:before, blockquote:after, q:before, q:after {content: '';content: none;}
table {border-collapse: collapse;border-spacing: 0;}
/* webkit引擎的浏览器取消默认样式(-webkit-appearance) */
input,textarea{-webkit-user-select:auto !important;user-select:auto !important;/*-webkit-appearance:none;*/}
:focus{outline:none;}
img{vertical-align:middle;border:0 none;}
a{text-decoration:none;color:inherit;}
section{display:block;margin:0 auto;}
header,footer{margin:0 auto;width:100%;}
em,i{font-style:normal;}
button,input{border:0 none; padding: 0}
u,del{text-decoration:none;}
/* webkit引擎的浏览器取消默认样式 */
input[type="submit"],input[type="reset"],input[type="button"],input[type="password"],input[type="text"],input[type="tel"],button{-webkit-appearance:none;outline:none;}
.fixed:after { content: "."; display: block; clear: both; height: 0; visibility: hidden; }
.fixed { display: block; min-height: 1%; }
.clear { diplay: block!important; float: none!important; clear: both; overflow: hidden; width: auto!important; height: 0!important; margin: 0 auto!important; padding: 0!important; font-size: 0; line-height: 0; }
/*基于display:table-cell的自适应布局*/
.cell{display:table-cell; *display:inline-block; width:2000px; *width:auto;}
/*双栏自适应cell部分连续英文字符换行*/
.cell_bk{display:table; width:100%; table-layout:fixed; word-wrap:break-word;}
/*单行文字溢出虚点显示*/
.ell{text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}
html, a {-webkit-tap-highlight-color: transparent}
textarea.fixAndroidKeyboard:focus, input.fixAKeyboard:focus {-webkit-tap-highlight-color: rgba(255,255,255,0);-webkit-user-modify: read-write-plaintext-only}
html, body {line-height: 1;width: 100%;font-family: Arial,Helvetica,STHeiTi,sans-serif;-webkit-text-size-adjust: none;  }
.nwrapper{width: 100%; min-width: 320px; max-width: 750px; position: relative; margin: 0 auto;}
input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
    background-color: #fff;
    background-image: none;
    color: rgb(0, 0, 0);
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}
/*分割*/
.nhr{ width: 100%; height: .2rem; background: #f2f2f2}
/*弹窗样式*/
.dialog-cart {
    position: fixed;
    top: 50%;
    width: 100%;
    height: auto;
    z-index: 999;
    max-width: 750px;
}
.dialog-cart .dialog-cart-inner {
    background: rgba(0, 0, 0, 0.7);
    position: relative;
    overflow: hidden;
    width: 270px;
    height: auto;
    margin: 0 auto;
    padding: 10px;
    border-radius: 6px;
}
.dialog-cart .dialog-cart-inner span {
    width: 270px;
    height: auto;
    float: left;
    font-size: 16px;
    overflow: hidden;
    color: #ffffff;
    line-height: 24px;
    text-align: center;
}
/*
图片懒加载css
*/
@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }
    50% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fadein {
    0% {
        opacity: 0;
    }
    50% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}
.data-img_fadein {
    -webkit-animation: fadein 1s ease-in 1;
            animation: fadein 1s ease-in 1;
}
/*禁止body滚动*/
.hidescroll {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
/* 阴影层 */
.cmp-fixed {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .6);
    z-index: 888;
    top: 0;
    left: 0;
    display: none;
}
.cover-mask-toggle {
    display: block;
    background: rgba(0, 0, 0, .5);
}
/*失效遮盖层*/
.lose-goods{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.3);
    text-align: center;
    font-size: .3rem;
    color: #444444;
    display: -webkit-flex;
    display: flex;
    display: -webkit-box;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-box-align: center;
    -webkit-box-pack: center;
}